*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html,
body {
  height: 100%; }

body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 0;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  color: #102654; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

a:hover {
  opacity: 0.4; }

input::-webkit-input-placeholder {
  overflow: visible; }

input::-moz-placeholder {
  overflow: visible; }

input:-ms-input-placeholder {
  overflow: visible; }

input::-ms-input-placeholder {
  overflow: visible; }

input::placeholder {
  overflow: visible; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  /* 1 */
  border: 0;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%); }
